import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Observable } from 'rxjs';
import { ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-tcsoffdetails',
  templateUrl: './tcsoffdetails.component.html',
  styleUrls: ['./tcsoffdetails.component.scss']
})
export class TcsoffdetailsComponent implements OnInit {
pincode$:Object;
office$:Object;

constructor(private data:DataService,private route:ActivatedRoute) {
    this.route.params.subscribe( params => this.pincode$ = params.name ); 
  }

  ngOnInit() {
    this.data.getTcs().subscribe(
      (data:any) =>
      {
          for(let office of data)
          {
            if(this.pincode$==office.name)
            {
              this.office$=office;
              break;
            }
          }
      });
}
}

