import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Observable } from 'rxjs';
@Component({
  selector: 'app-tcsoffices',
  templateUrl: './tcsoffices.component.html',
  styleUrls: ['./tcsoffices.component.scss']
})
export class TcsofficesComponent implements OnInit {
  offices$:object;
  constructor(private data:DataService) { }

  ngOnInit() {
    this.data.getTcs().subscribe(
      data => this.offices$ = data
    );
  }

}
